@import url(https://fonts.googleapis.com/css?family=Ubuntu);


.main-container{
    display: flex;
    flex-direction: column;

    color: white;
    text-align: left;
    font-family: 'Ubuntu', sans-serif;

}
.about{
    flex: 1;


}
.about p{
  padding: 0px 16px;  
}

.skills{
    flex: 1; 

}
