    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.skill-card img {
  width: 100%;
  height: 100%;
  object-fit: contain;     /* âœ… Î´Î¹Î±Ï„Î·ÏÎµÎ¯ Ï„Î¹Ï‚ Î±Î½Î±Î»Î¿Î³Î¯ÎµÏ‚ Ï‡Ï‰ÏÎ¯Ï‚ Ï€Î±ÏÎ±Î¼ÏŒÏÏ†Ï‰ÏƒÎ· */
