  flex-wrap: nowrap; /* Î”ÎµÎ½ ÎµÏ€Î¹Ï„ÏÎ­Ï€ÎµÎ¹ Î½Î± Ï€Î¬Î½Îµ ÏƒÎµ Î´ÎµÏÏ„ÎµÏÎ· Î³ÏÎ±Î¼Î¼Î® */
    
}
@media (max-width: 1024px) {
  .main-container {
    flex-direction: column;
    text-align: center;
  }
  
  .skills-grid {
    flex-wrap: wrap;
    justify-content: center;
  }

  .skill-card {
    padding: 0 0;
    width: 80px;
    height: 80px;
  }
  .about p{
    text-align: center;
  }
}

@media (max-width: 480px) {
  .skill-card {
    padding: 0 0;
    width: 60px;
    height: 60px;
  }

  .about {
    text-align: center;
  }
}
