/* Section spacing: Ï€ÏŒÏƒÎ¿ Î±Ï€Î­Ï‡ÎµÎ¹ Î±Ï€ÏŒ Ï„Î¿ banner Ï€Î¬Î½Ï‰ ÎºÎ±Î¹ Î±Ï€ÏŒ Ï„Î± Skills ÎºÎ¬Ï„Ï‰ */
.pj-wrapper{
  max-width: 1024px;
  width: 100%;
  margin: 2em auto;

}
.projects-section {
  width: 100%;
  max-width: 1200px;
  margin: 48px auto 56px;   
  
}

.projects-section h2 {
  font-size: 26px;
  color: white;
  margin-bottom: 24px;
}

/* === Î‘Ï€Î»ÏÎ½Î¿Ï…Î¼Îµ Ï„Î¹Ï‚ ÎºÎ¬ÏÏ„ÎµÏ‚ Î¼Îµ Grid === */
.pj {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 24px;
}

/* Î— ÎºÎ¬ÏÏ„Î± Î³ÎµÎ¼Î¯Î¶ÎµÎ¹ Ï„Î¿ Î´Î¹Î±Î¸Î­ÏƒÎ¹Î¼Î¿ Ï€Î»Î¬Ï„Î¿Ï‚ Ï„Î·Ï‚ ÏƒÏ„Î®Î»Î·Ï‚ */
.projects-card {
  /* Î±Ï†Î±Î¹ÏÎ¿ÏÎ¼Îµ Ï„Î± flex/max-width ÏÏƒÏ„Îµ Î½Î± Î±Ï€Î»ÏÎ½ÎµÏ„Î±Î¹ */
  background: #2a2a2a;
  border-radius: 10px;
  padding: 2px;
  text-align: center;
  box-shadow: 0 2px 8px rgba(0,0,0,.3);
  transition: transform .3s ease;
  height: 100%;
  
}

.projects-card:hover { transform: translateY(-3px); }

.projects-card img {
  width: 100%;
  height: 200px;              /* ÎºÏÎ±Ï„Î¬ Ï„Î¿ Î¯Î´Î¹Î¿ ÏÏˆÎ¿Ï‚ ÏŒÏ€Ï‰Ï‚ Ï†Î±Î¯Î½ÎµÏ„Î±Î¹ Ï„ÏÏÎ± */
  object-fit: contain;        /* âœ… Î´ÎµÎ¯Ï‡Î½ÎµÎ¹ ÎŸÎ›Î— Ï„Î·Î½ ÎµÎ¹ÎºÏŒÎ½Î± Ï‡Ï‰ÏÎ¯Ï‚ Î½Î± ÎºÏŒÎ²ÎµÏ„Î±Î¹ */
  background-color: #1a1a1a;  /* Ï€ÏÎ¿Î±Î¹ÏÎµÏ„Î¹ÎºÎ¬: Î´Î¹Î±ÎºÏÎ¹Ï„Î¹ÎºÏŒ Ï†ÏŒÎ½Ï„Î¿ Î³ÏÏÏ‰ Î±Ï€ÏŒ Ï„Î·Î½ ÎµÎ¹ÎºÏŒÎ½Î± */
  border-radius: 8px;
  display: block;
  margin: 0 auto;
 
}
.projects-card a {
  text-decoration: none;


}

.projects-card h3 { margin-top: 12px; font-size: 18px; color: #fff; text-decoration: none;}
.projects-card p  {text-decoration: none; margin-top: 6px;  font-size: 14px; color: #ddd; }

@media (max-width: 768px)  {
  .projects-section h2 {
    font-size: 24px;
    margin-bottom: 20px;
  }

  .pj {
    grid-template-columns: 1fr;  /* ÎœÎ¯Î± ÏƒÏ„Î®Î»Î· */
    gap: 24px;
  }

  .projects-card {
    margin: 0 auto;
    max-width: 90%;
  }
}

@media (max-width: 480px) {
  .projects-section h2 {
    font-size: 22px;
  }

  .projects-card {
    max-width: 95%;
  }

  .projects-card img {
    height: 180px;
  }
}
/* ============ TABLET VIEW (768px - 1024px) ============ */
@media (min-width: 768px) and (max-width: 1024px) {

  .projects-section {
    max-width: 950px;
    margin: 0 auto;
    padding: 20px 0;
    text-align: center;
  }

  .projects-section h2 {
    font-size: 26px;
    margin-bottom: 24px;
  }



  /* === ÎšÎ‘Î¡Î¤Î•Î£ === */
  .projects-card {
    margin: 0 auto;
    width: 100%;
    max-width: 420px;            /* Î³Î¹Î± Î¹ÏƒÎ¿ÏÏÎ¿Ï€Î·Î¼Î­Î½ÎµÏ‚ ÎºÎ¬ÏÏ„ÎµÏ‚ */
    background-color: #1f1f1f;
    border-radius: 10px;
    overflow: hidden;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }

  .projects-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.4);
  }

  .projects-card img {
    width: 100%;
    height: 200px;               /* ÏƒÏ„Î±Î¸ÎµÏÏŒ ÏÏˆÎ¿Ï‚ Î³Î¹Î± ÎºÎ±Î¸Î±ÏÏŒ layout */
    object-fit: cover;
    display: block;
  }

  .projects-card h3 {
    font-size: 18px;
    color: #fff;
    margin: 12px 0 6px;
  }

  .projects-card p {
    font-size: 14px;
    color: #ccc;
    margin-bottom: 16px;
  }
}
